@import "main-bx";

.popup {

  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(44px);
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all 0.8s ease 0s;
  z-index: 51;

  &.open {
    opacity: 1;
    visibility: visible;

    .popup__content {
      transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
      opacity: 1;
    }
  }

  &__body {
    min-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 10px;
  }

  &__content {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 950px;

    border-radius: 24px;
    box-shadow: 0px 20px 80px 0px #00000033;
    background-color: $color_white;

    overflow: hidden;

    transition: all 0.8s ease 0s;
    opacity: 0;
    transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
  }

  &__close {
    position: absolute;
    content: '';
    top: 18px;
    right: 23px;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;

    background-color: #D9D9D9;
    border-radius: 50%;

    @include m-phone() {
      top: 14px;
      right: 14px;
      width: 40px;
      height: 40px;
    }
  }

  &__left {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 50%;

    padding: 24px 66px 6px 90px;

    @include m-tablet() {
      padding: 30px;
    }

    @include m-phone() {
      padding: 30px 50px;
      width: 100%;
    }

    @include below(501px) {
      padding: 30px;
    }
  }

  &__logo {
    margin-bottom: 26px;
    width: 130px;

    @include m-phone() {
      margin-bottom: 12px;
    }
  }

  &__title {
    margin-bottom: 13px;

    font-weight: 600;
    font-size: 40px;
    line-height: 120%;
    text-align: center;

    @include below(501px) {
      font-size: 32px;
    }
  }

  &__sub-title {
    margin-bottom: 39px;
    max-width: 310px;
    width: 100%;

    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    text-align: center;
    color: #000000;

    @include m-phone() {
      margin-bottom: 20px;
      max-width: 100%;
    }

    @include below(501px) {
      font-size: 16px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    color: #8D8D8D;
  }

  &__img {
    width: 50%;

    @include m-phone() {
      display: none;
    }
  }

  &__img img {
    width: 100%;
    vertical-align: top;
  }
}
