@import "main-bx";

.voucher {

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 14px;
    padding: 14px 19px;

    border-radius: 12px;
    background-color: $color_white;
    box-shadow: 0px 40px 80px -16px #00000014;

    @include m-phone() {
      padding: 10px 12px;
    }
  }

  &__icon img {
    @include m-phone() {
      width: 36px;
      height: 36px;
    }
  }

  &__title {
    margin-bottom: 5px;

    font-weight: 700;
    font-size: 17px;
    line-height: 120%;

    @include m-phone() {
      font-size: 14px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 11px;
    line-height: 120%;
    color: $color_gray3;
  }
}
