@import "main-bx";

.offer {
  margin: 60px 0;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 32px;

    @include below(950px) {
      flex-direction: column;
    }
  }

  &__title {
    margin-bottom: 8px;

    font-weight: 700;
    font-size: 32px;
    line-height: 140%;

    @include below(950px) {
      text-align: center;
    }

    span {
      color: $color_violet;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 180%;
    color: $color_gray4;

    @include below(950px) {
      text-align: center;
    }
  }

  &__button {
    padding: 18px 32px;
    height: auto;
    font-size: 16px;
    line-height: 100%;

    @include m-phone() {
      padding: 14px 28px;
      height: auto;
    }
  }
}
