@import "main-bx";

.video {
  position: relative;
  width: 66%;
  margin: 0 auto;
  padding: 0 0 37% 0;

  @include m-phone() {
    width: 80%;
    padding: 0 0 45% 0;
  }

  @include s-phone() {
    width: 100%;
    padding: 0 0 56% 0;
  }

  &__item {
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 16px;
    box-shadow: 0px 20px 80px 0px #00000033;
  }

  &__icon {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 72px;
    height: 72px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $color_white;
    border-radius: 50%;

    &:hover {
      -webkit-box-shadow: -1px 1px 10px 8px rgba(255, 255, 255, 0.7);
      -moz-box-shadow: -1px 1px 10px 8px rgba(255, 255, 255, 0.7);
      box-shadow: -1px 1px 10px 8px rgba(255, 255, 255, 0.7);
    }

    &_off {
      display: none;
    }
  }

  &__voucher {
    position: absolute;
    content: '';
    top: -50px;
    left: -144px;

    @include m-phone() {
      top: -64px;
      left: -68px;
    }

    @include s-phone() {
      left: -6px;
    }
  }

  &__sent {
    position: absolute;
    content: '';
    bottom: -58px;
    right: -192px;
    z-index: 1;

    @include below(1200px) {
      bottom: -106px;
      right: -134px;
    }

    @include m-phone() {
      bottom: -96px;
      right: -52px;
    }

    @include s-phone() {
      bottom: -104px;
      right: -6px;
    }
  }

  &__arrow {
    position: absolute;
    content: '';
    bottom: -88px;
    left: -182px;

    @include m-tablet() {
      left: -150px;
    }

    @include m-phone() {
      display: none;
    }
  }

}
