@import "main-bx";

.sent {

  &__wrapper {
    padding: 17px 22px;

    border-radius: 12px;
    background-color: $color_white;
    box-shadow: 0px 40px 80px -16px #00000014;

    @include m-phone() {
      padding: 10px 12px;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 14px;
  }

  &__icon img {
    @include m-phone() {
      width: 36px;
      height: 36px;
    }
  }

  &__title {
    margin-bottom: 5px;

    font-weight: 700;
    font-size: 17px;
    line-height: 120%;

    @include m-phone() {
      font-size: 14px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 11px;
    line-height: 120%;
    color: $color_gray3;
  }

  &__bottom {
    margin-top: 14px;
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  &__item {
    width: 36px;
    height: 36px;
    flex-shrink: 0;

    border-radius: 50%;

    @include m-phone() {
      width: 34px;
      height: 34px;
    }

    &:not(:first-child) {
      margin-left: -10px;
    }
    &:not(:first-child),
    &:not(:last-child) {
      border: 3px solid #FFFFFF;
    }
  }
}
