@import "main-bx";

.optimize {
  margin: 96px 0 61px;
  padding: 96px 0;
  background-color: #F9F9FB;

  @include s-phone() {
    margin: 60px 0 60px;
    padding: 60px 0;
  }

  &__sub-title {
    margin-bottom: 10px;
  }

  &__title {
    margin-bottom: 42px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 40.4% 48.95%;
    grid-gap: 120px;

    @include m-tablet() {
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 60px;
    }
  }

  &__item-title {
    margin-bottom: 12px;

    font-weight: 700;
    font-size: 20px;
    line-height: 140%;
    color: $color_violet;
  }

  &__item-text {
    max-width: 456px;
    width: 100%;

    font-weight: 400;
    font-size: 16px;
    line-height: 180%;
    color: $color_gray4;
  }

  &__item-img {
    margin: 32px 0;
  }

  &__item-list {

  }

  &__item-list li {
    position: relative;
    padding-left: 31px;

    font-weight: 400;
    font-size: 14px;
    line-height: 180%;

    &:not(:last-child) {
      margin-bottom: 6px;
    }

    &:before {
      position: absolute;
      content: '';
      background-image: url("../img/svg/icon-check.svg");
      background-size: 20px 20px;
      width: 20px;
      height: 20px;
      top: 0;
      left: 0;
    }
  }
}
