@import "main-bx";

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: 40px;
  padding: 8px 24px;
  border-radius: 6px;
  background-color: $color_violet;
  font-family: $font_base;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  color: $color_white;

  @include m-phone() {
    height: 30px;
    padding: 6px 18px;
  }

  &:hover,
  &:focus,
  &:active {
    opacity: 0.7;
  }

  &:disabled {
    opacity: 0.3;
  }

  &_white {
    background-color: $color_white;
    color: $color_gray;
  }

}
