@import "main-bx";

.intro {
  margin: 68px 0;

  @include s-phone() {
    margin: 40px 0;
  }

  &__title {
    position: relative;
    max-width: 910px;
    width: 100%;
    margin: 0 auto 20px;
    font-weight: 700;
    font-size: 56px;
    line-height: 120%;
    text-align: center;

    @include m-tablet() {
      max-width: 100%;
    }

    @include m-phone() {
      font-size: 42px;
    }

    @include s-phone() {
      font-size: 32px;
    }

    &:before {
      position: absolute;
      content: '';
      background-image: url("../img/content/part.png");
      background-size: contain;
      width: 67px;
      height: 67px;
      top: 28px;
      left: -174px;
      z-index: -1;

      @include below(1380px) {
        left: -136px;
      }

      @include below(1200px) {
        left: -56px;
      }

      @include m-tablet() {
        left: -10px;
      }

      @include m-phone() {
        width: 50px;
        height: 50px;
        top: -20px;
      }
    }

    &:after {
      position: absolute;
      content: '';
      background-image: url("../img/content/memoji.png");
      background-size: contain;
      width: 32px;
      height: 32px;
      bottom: 26px;
      right: -32px;
      z-index: -1;

      @include m-tablet() {
        right: 0;
      }

      @include m-phone() {
        width: 28px;
        height: 28px;
      }
    }
  }

  &__text {
    margin: 0 auto 72px;
    font-weight: 400;
    font-size: 20px;
    line-height: 180%;
    color: $color_gray2;
    text-align: center;

    @include s-phone() {
      font-size: 16px;
      line-height: 150%;
      max-width: 86%;
    }
  }
}
