@import "main-bx";

.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color_white;
    z-index: 2;
  }

  &__body {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 104px;

    @include m-phone() {
      justify-content: space-between;
      height: 50px;
    }
  }

  &__logo {
    position: relative;
    z-index: 3;
    flex: 0 0 128px;

    @include m-phone() {
      flex: 0 0 120px;
    }
  }

  &__logo svg {
    max-width: 100%;
    display: block;
  }

  &__menu {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    @include m-phone() {
      position: fixed;
      top: -100%;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $color_white;
      padding: 70px 10px 20px 10px;
      transition: all 0.3s ease 0s;
      overflow: auto;
    }

    &.active {
      display: block;
      @include m-phone() {
        top: 0;
      }
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: 2;

    @include m-phone() {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__list li {
    list-style: none;

    &:not(:last-child) {
      margin: 0 48px 0 0;

      @include m-phone() {
        margin: 0 0 20px 0;
      }
    }
  }

  &__authorization {

    @include m-phone() {
      margin-top: 40px;
    }

    li {
      &:not(:last-child) {
        margin: 0 10px 0 0;

        @include m-phone() {
          margin: 0 0 20px 0;
        }
      }
    }
  }

  &__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color_gray;
    opacity: 0.7;

    &:hover {
      color: $color_violet;
      opacity: 1;
    }
  }

  &__authorization {
    margin-left: auto;
    flex-shrink: 0;
  }

  &__burger {
    display: none;

    @include m-phone() {
      display: block;
      position: relative;
      width: 30px;
      height: 20px;
      z-index: 3;
    }

    span {
      position: absolute;
      left: 0;
      top: 9px;
      background-color: $color_black;
      width: 100%;
      height: 2px;
      transition: all 0.3s ease 0s;
    }

    &::before,
    &::after {
      content: '';
      background-color: $color_black;
      position: absolute;
      width: 100%;
      height: 2px;
      left: 0;
      transition: all 0.3s ease 0s;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }

    &.active {

      &::before {
        transform: rotate(45deg);
        top: 9px;
      }

      &::after {
        transform: rotate(-45deg);
        bottom: 9px;
      }

      span {
        transform: scale(0);
      }
    }

  }
}
