@import "main-bx";

.form {
  width: 100%;
  margin-bottom: 22px;

  &__input {
    margin-bottom: 30px;
    padding: 12px 19px;
    width: 100%;

    font-weight: 400;
    font-size: 20px;
    line-height: 120%;

    border: 1px solid #D9D9D9;
    border-radius: 10px;
    background-color: $color_white;

    @include m-phone() {
      margin-bottom: 20px;
    }

    &::placeholder {
      color: #898989;
    }
  }

  &__button {
    width: 100%;
    height: auto;
    padding: 14px 24px;

    font-weight: 600;
    font-size: 20px;
    line-height: 120%;

    background-color: #000000;

    @include m-phone() {
      height: auto;
      padding: 14px 24px;
    }
  }
}
