@import "main-bx";

.footer {
    margin: 56px 0;

    &__wrapper {
        padding-bottom: 70px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 24px;

        border-bottom: 1px solid #1211271F;

        @include below(950px) {
            flex-direction: column-reverse;
            gap: 40px;
        }
    }

    &__info {
        max-width: 360px;
        width: 100%;

        @include below(950px) {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            max-width: 100%;
        }
    }

    &__logo {
        margin-bottom: 24px;

        display: block;
    }

    &__text {
        margin-bottom: 24px;

        font-weight: 400;
        font-size: 14px;
        line-height: 180%;
        color: $color_gray4;

        @include below(950px) {
            text-align: center;
        }
    }

    &__social-list {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 32px;
    }

    &__social-item {
        width: 24px;
        height: 24px;

        a {
            color: $color_gray3;

            &:hover {
                color: $color_violet;
            }
        }

    }

    &__menu {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;
        gap: 20px;

        width: 100%;
    }

    &__menu-col {
        width: calc(100% / 4 - 20px);

        @include below(950px) {
            width: 100%;
        }
    }

    &__menu-list {

        @include below(950px) {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            gap: 2px 14px;
        }
    }

    &__menu-item {
        font-weight: 400;
        font-size: 14px;
        line-height: 180%;

        a {
            color: $color_gray;

            &:hover {
                color: $color_violet;
            }
        }

        &:not(:last-child) {
            margin-bottom: 8px;

            @include below(950px) {
                margin-bottom: 0;
            }
        }

    }

    &__menu-name {
        margin-bottom: 20px;

        font-weight: 600;
        font-size: 14px;
        line-height: 170%;
        letter-spacing: 0.8px;
        text-transform: uppercase;
        color: #12112766;

        @include below(950px) {
            margin-bottom: 10px;
            text-align: center;
        }

    }

    &__copyright {
        padding: 37px 0 40px;

        font-weight: 400;
        font-size: 16px;
        line-height: 180%;
        text-align: center;
        color: $color_gray4;
    }
}
