@import "main-bx";

.boost {
  padding: 96px 0;
  background-color: $color_violet;

  &__title {
    margin: 0 auto 20px;
    max-width: 620px;
    width: 100%;

    font-weight: 700;
    font-size: 44px;
    line-height: 140%;
    text-align: center;
    color: $color_white;

    @include m-phone() {
      font-size: 36px;
    }

    @include s-phone() {
      font-size: 32px;
    }
  }

  &__text {
    margin: 0 auto 20px;
    max-width: 744px;
    width: 100%;

    font-weight: 400;
    font-size: 20px;
    line-height: 180%;
    text-align: center;
    color: #FFFFFF8F;

    @include m-phone() {
      font-size: 18px;
    }
  }

  &__button {
    margin: 0 auto;
    padding: 18px 32px;
    height: auto;
    font-size: 16px;
    line-height: 100%;

    @include m-phone() {
      padding: 14px 28px;
      height: auto;
    }
  }
}
