@import "main-bx";

.advantages {
  margin: 188px 0 96px;

  @include s-phone() {
    margin: 140px 0 0;
  }

  &__container {
    overflow-x: hidden;
  }

  &__sub-title {
    margin-bottom: 8px;
  }

  &__title {
    max-width: 604px;
    width: 100%;
  }

  &__slider {
    position: relative;
    margin-top: 38px;
  }

  &__slider-slide {
    padding: 56px 24px;
    border-radius: 12px;
    background-color: $color_white;
    box-shadow: 0px 20px 50px 0px #12112714;
  }

  &__icon {
    margin-bottom: 24px;
  }

  &__name {
    margin-bottom: 16px;

    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
  }

  &__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 180%;
    color: $color_gray4;
  }

  &__controls {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;
    margin-top: 37px;
  }

  &__button-prev,
  &__button-next {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #FFF0F5;

    &.swiper-button-disabled {
      opacity: 0.4;
    }
  }

  &__pagination {
    display: flex;
    align-items: center;
    justify-content: center;

    &.swiper-pagination-bullets.swiper-pagination-horizontal {
      width: auto;
    }

    .swiper-pagination-bullet {
      width: 4px;
      height: 4px;
      background: #32334C;
      opacity: 1;
      flex-shrink: 0;
      transition: all 0.1s ease 0s;
    }
    .swiper-pagination-bullet-active {
      position: relative;
      width: 24px;
      height: 24px;
      background: $color_white;
      border: 1px solid #9D097C;
      border-radius: 50%;

      &:before {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: #9D097C;
      }
    }
  }
}
